:root {
  --aqua-marin: #54e6af;
  --pale-cyan: #b3ffe2;
  --dark-blue-grey: #2c344b;
  --mirage: #121725;
  --white: #ffffff;
  --marble-blue: #5a668a;
  --light-blue-grey: #c2cbe5;
  --red: #fb3e3e;
}

.text-aqua-marin {
  color: var(--aqua-marin);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Chivo", sans-serif;
  font-weight: 300;
  min-height: 100vh;
  background: bottom 10% right/14.5rem 6.5rem no-repeat url(/assets/desktop/bg-pattern-dots.svg), right/55.5rem 40rem no-repeat url(/assets/desktop/image-host.jpg), var(--mirage);
}
@media (max-width: 63.9375em) {
  body {
    background: left 5% bottom/14.5rem 6.5rem no-repeat url(/assets/desktop/bg-pattern-dots.svg), top right/30.6875rem 47.9375rem no-repeat url(/assets/tablet/image-host.jpg), var(--mirage);
  }
}
@media (max-width: 39.9375em) {
  body {
    background: center/cover no-repeat url(/assets/mobile/image-host.jpg), var(--mirage);
    background-blend-mode: overlay;
  }
}

.title {
  font-size: 3.25rem;
  font-weight: 300;
  line-height: 3.875rem;
  text-transform: uppercase;
  color: var(--white);
}
@media (max-width: 63.9375em) {
  .title {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}
@media (max-width: 39.9375em) {
  .title {
    font-size: 1.625rem;
    line-height: 2.375rem;
  }
}

body {
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 1.75rem;
  color: var(--light-blue-grey);
}
@media (max-width: 39.9375em) {
  body {
    font-size: 0.9375rem;
    line-height: 1.5625rem;
  }
}

.container {
  max-width: 1110px;
  width: 77.0833333333%;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .container {
    width: 89.84375%;
  }
}
@media (max-width: 39.9375em) {
  .container {
    width: 87.2%;
  }
}

.header {
  padding-top: 6.375rem;
  padding-bottom: 6.375rem;
}
@media (max-width: 63.9375em) {
  .header {
    padding-top: 3.125rem;
    padding-bottom: 9.5rem;
  }
}
@media (max-width: 39.9375em) {
  .header {
    padding-top: 3.875rem;
    padding-bottom: 3.5625rem;
    text-align: center;
  }
}

.wrapper {
  width: 45.1875rem;
  height: 31.8125rem;
  background: var(--mirage);
  padding-top: 5.5rem;
  margin-bottom: 8.125rem;
}
@media (max-width: 63.9375em) {
  .wrapper {
    width: 39.6875rem;
    padding-top: 5.8125rem;
    margin-bottom: 9.625rem;
  }
}
@media (max-width: 39.9375em) {
  .wrapper {
    width: 100%;
    height: 100%;
    background: transparent;
    padding-top: 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-bottom: 5.875rem;
  }
}
.wrapper .title {
  margin-bottom: 1.5rem;
}
@media (max-width: 39.9375em) {
  .wrapper .title {
    margin-bottom: 1rem;
  }
}
.wrapper .text {
  width: 27.8125rem;
  margin-bottom: 2.5rem;
}
@media (max-width: 39.9375em) {
  .wrapper .text {
    width: 100%;
    margin-bottom: 2.0625rem;
  }
}
.wrapper .form {
  margin-bottom: 4rem;
}
.wrapper .form.invalid {
  margin-bottom: 1.75rem;
}
@media (max-width: 39.9375em) {
  .wrapper .form {
    order: 10;
  }
}
.wrapper .companies {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 33.5rem;
}
@media (max-width: 39.9375em) {
  .wrapper .companies {
    width: 100%;
    order: 1;
    margin-bottom: 2.0625rem;
  }
  .wrapper .companies :first-child {
    width: 3.5rem;
  }
  .wrapper .companies :nth-child(2) {
    width: 2.8125rem;
  }
  .wrapper .companies :nth-child(3) {
    width: 4.5625rem;
  }
  .wrapper .companies :last-child {
    width: 4.8125rem;
  }
}

.button {
  font-family: inherit;
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--mirage);
  padding: 0.5625rem 1.6875rem;
  background: var(--aqua-marin);
  border: none;
  width: 10.125rem;
  height: 2.875rem;
  border-radius: 1.75rem;
  cursor: pointer;
}
.button:hover {
  background: var(--pale-cyan);
}

.form {
  width: 26.6875rem;
  position: relative;
}
@media (max-width: 39.9375em) {
  .form {
    width: 100%;
  }
  .form > * + * {
    margin-top: 1rem;
  }
}
.form .email-input {
  width: 100%;
  height: 3.5rem;
  padding: 0.875rem 10.625rem 0.875rem 2rem;
  background: var(--dark-blue-grey);
  color: var(--white);
  border: none;
  outline: none;
  border-radius: 1.75rem;
}
.form .email-input::placeholder {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--light-blue-grey);
}
@media (max-width: 39.9375em) {
  .form .email-input {
    height: 2.875rem;
    padding: 0.5625rem 2rem;
  }
}
.form .button {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-0.3125rem, 0.3125rem);
}
@media (max-width: 39.9375em) {
  .form .button {
    position: initial;
    transform: initial;
    width: 100%;
    height: 2.875rem;
  }
}
.form.invalid:after {
  display: block;
  content: "Oops! Please check your email";
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--red);
  text-indent: 2rem;
  margin-top: 0.5rem;
}